import React, { useEffect, useRef } from "react";
import "./App.scss";
import html2canvas from "html2canvas";
import {
  Card,
  Grid,
  Typography,
  Fab,
  Icon,
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
  TextField,
  Autocomplete,
  FormControl,
  InputAdornment,
} from "@mui/material";
import { Container } from "@mui/system";
const { amarillo, azul, rojo, verde, morado, naranja } = {
  amarillo: "#F9A51A",
  azul: "#034EA1",
  rojo: "#ED2424",
  verde: "#32AA49",
  morado: "#8F2F91",
  naranja: "#E64610",
};

const Nombre: React.FC<{ nombre: string; hashtag: string; fontSize: number }> = ({ nombre, hashtag, fontSize }) => {
  return (
    <Grid item xs={12}>
      <h5 style={{ color: naranja, fontSize: fontSize * 0.6 }} className="quiero">
        <b>Me llamo </b>
      </h5>
      {nombre.length < 10 ? (
        <h1 className="logo noselect  " style={{ margin: 0, padding: 0, fontSize: fontSize }}>
          {colorearString(nombre)}
        </h1>
      ) : (
        nombre.split(" ").map((nombre: string, index: number) => (
          <h1 className="logo noselect  " style={{ margin: 0, padding: 0 }}>
            {colorearString(nombre)}
          </h1>
        ))
      )}

      <Typography sx={{ color: naranja, marginTop: "1em !important", fontSize: fontSize / 2 }} className="quiero">
        <b>y quiero </b>
        <br />
        <span style={{ color: morado }}>#</span>
        <span className="hashtag" style={{ color: morado, marginTop: "2em" }}>
          {hashtag}
        </span>
      </Typography>
    </Grid>
  );
};

const Imagen: React.FC<{
  hashtag: string;
  nombre: string;
  ratio: string;
  background: string;
  onSave: any;
  onReady?: any;
}> = ({ hashtag, nombre, ratio, background, onSave, onReady }) => {
  const [randomId] = React.useState(Math.random());

  const [parentWidth, setParentWidth] = React.useState(1);

  const padre: any = useRef(null);
  useEffect(() => {
    setParentWidth(Math.round(padre.current.clientWidth));
    if (onReady) onReady(true);
  }, []);

  return (
    <>
      <Card
        ref={padre}
        elevation={2}
        id={"image" + randomId}
        style={{
          aspectRatio: ratio,
          background: `url('./ig/${background}')`,
          backgroundSize: "contain",
          position: "relative",
        }}
      >
        <div
          className="image"
          style={{ position: "relative", marginTop: "6%", marginLeft: "auto", marginRight: "auto", width: "100%" }}
        >
          <Nombre nombre={nombre} hashtag={hashtag} fontSize={parentWidth / 6} />
        </div>
        <Fab
          className="fab"
          onClick={() => onSave({ id: "image" + randomId, background: background, ratio: ratio })}
          color="primary"
          aria-label="Descargar"
          style={{ position: "absolute", zIndex: 30, bottom: "1em", right: "1em" }}
        >
          <Icon>download</Icon>
        </Fab>
      </Card>
    </>
  );
};
function App() {
  const [nombre, setNombre] = React.useState("TU NOMBRE");
  const [hashtag, setHashtag] = React.useState("PACTOPORLAVIDA");
  const [descargable, setDescargable] = React.useState(false);
  const [idDescargar, setIdDescargar] = React.useState("");
  const [imagen, setImagen] = React.useState<{ id: string; background: string; ratio: string } | null>(null);

  const saveImage = ({ id, background, ratio }: any) => {
    setImagen({ id, background, ratio });
    let fab = document?.getElementsByClassName("fab") as any;
    //hide all fabs
    for (let i = 0; i < fab.length; i++) {
      fab[i].style.display = "none";
    }
    html2canvas(document?.getElementById("gigante") as any)
      .then(function (canvas) {
        document.body.appendChild(canvas);

        canvas.style.display = "none";

        const img = canvas.toDataURL("image/png");
        const link = document.createElement("a");
        link.href = img;

        link.download = "image.png";
        link.click();
      })
      .then((_) => {
        //show all fabs
        for (let i = 0; i < fab.length; i++) {
          fab[i].style.display = "block";
        }
        setImagen(null);
        setDescargable(false);
      });
  };

  useEffect(() => {
    if (descargable) saveImage(idDescargar);
  }, [descargable]);

  const [open, setOpen] = React.useState(true);
  return (
    <>
      {imagen?.id && (
        <div style={{ width: "1920px", position: "absolute", zIndex: 3000 }} id="gigante">
          <Imagen
            nombre={nombre}
            hashtag={hashtag}
            ratio={imagen?.ratio as string}
            background={imagen?.background as string}
            onSave={saveImage}
            onReady={() => setDescargable(true)}
          />
        </div>
      )}
      <Container>
        <div>
          <Button
            style={{ marginBottom: "4em" }}
            variant="contained"
            fullWidth
            onClick={() => setOpen((prev) => !prev)}
          >
            Cambiar nombre
          </Button>

          <Dialog open={open} onClose={() => setOpen(false)}>
            <DialogTitle>¡Hola!</DialogTitle>
            <DialogContent>
              <DialogContentText>
                Aquí puedes generar imagenes de apoyo a la campaña de Petro para tus redes sociales
              </DialogContentText>
              <TextField
                autoFocus
                margin="dense"
                id="name"
                label="¿Cómo te llamas?"
                type="string"
                value={nombre}
                fullWidth
                variant="standard"
                onChange={(e) => setNombre(e.target.value)}
              />

              <TextField
                select
                value={hashtag}
                onChange={(e) => {
                  setHashtag(e.target.value);
                }}
                SelectProps={{
                  native: true,
                }}
                variant="standard"
              >
                {["PETROPRESIDENTE", "VIVIRSABROSO", "PACTOSABROSO", "CAMBIOPORLAVIDA"].map((option) => (
                  <option key={option} value={option}>
                    #{option}
                  </option>
                ))}
              </TextField>
            </DialogContent>
            <DialogActions>
              <Button variant="contained" onClick={() => setOpen(false)}>
                Generar
              </Button>
            </DialogActions>
          </Dialog>
        </div>

        <Grid container spacing={2}>
          <Grid item lg={4}>
            <Imagen
              nombre={nombre}
              hashtag={hashtag}
              ratio={"9/16"}
              background="Instagram story - 1.png"
              onSave={saveImage}
            />
          </Grid>
          <Grid item lg={4}>
            <Imagen
              nombre={nombre}
              hashtag={hashtag}
              ratio={"9/16"}
              background="Instagram story - 2.png"
              onSave={saveImage}
            />
          </Grid>
          <Grid item lg={4}>
            <Imagen
              nombre={nombre}
              hashtag={hashtag}
              ratio={"9/16"}
              background="Instagram story - 3.png"
              onSave={saveImage}
            />
          </Grid>

          <Grid item lg={6}>
            <Imagen
              nombre={nombre}
              hashtag={hashtag}
              ratio={"1/1"}
              background="Instagram post - 1.png"
              onSave={saveImage}
            />
          </Grid>
          <Grid item lg={6}>
            <Imagen
              nombre={nombre}
              hashtag={hashtag}
              ratio={"1/1"}
              background="Instagram post - 1.png"
              onSave={saveImage}
            />
          </Grid>
        </Grid>

        <Grid container>
          <Grid item xs={12}>
            <Button
              variant="text"
              color="primary"
              fullWidth
              size="large"
              onClick={() => {
                //open link in new tab
                window.open(
                  "https://www.facebook.com/sharer/sharer.php?u=https://jeanp117.github.io/meLlamoPetro//",
                  "_blank"
                );
              }}
            >
              Compartir en Facebook{" "}
            </Button>
            <Button
              variant="text"
              color="primary"
              fullWidth
              size="large"
              onClick={() => {
                //open link in new tab
                window.open("https://twitter.com/intent/tweet?text=https://jeanp117.github.io/meLlamoPetro/", "_blank");
              }}
            >
              Compartir en Twitter{" "}
            </Button>

            <Button
              variant="text"
              color="primary"
              fullWidth
              size="large"
              onClick={() => {
                //open link in new tab
                window.open(
                  "whatsapp://send?text=Crea%20tu%20imagen%20con%20tu%20nombre%20en%20https://jeanp117.github.io/meLlamoPetro/",
                  "_blank"
                );
              }}
            >
              Compartir en WhatsApp
            </Button>
          </Grid>

          <Grid item xs={12} lg={4}>
            <Typography variant="h6" align="center" gutterBottom>
              <a href="https://twitter.com/MadameSiimone/status/1521074853335506944">Créditos de las ilustraciones</a>
            </Typography>
          </Grid>
          <Grid item xs={12} lg={4}>
            <Typography variant="h6" align="center" gutterBottom>
              Versión 1.0
            </Typography>
          </Grid>
          <Grid item xs={12} lg={4}>
            <Typography variant="h6" align="center" gutterBottom>
              Hecho con ☕
            </Typography>
          </Grid>
        </Grid>
      </Container>
    </>
  );
}

//usando una lista de colores, asigna un color a cada letra
function colorearString(texto: string) {
  const colores = [
    amarillo,
    azul,
    rojo,
    verde,
    morado,
    verde,
    azul,
    morado,
    rojo,
    verde,
    azul,
    rojo,
    verde,
    morado,
    verde,
    azul,
    morado,
    verde,
    azul,
    morado,
    rojo,
    verde,
    azul,
    rojo,
    verde,
    morado,
    verde,
    azul,
    morado,
    verde,
    azul,
    morado,
    rojo,
    verde,
    azul,
    rojo,
    verde,
    morado,
    verde,
    azul,
    morado,
  ];
  return (
    <>
      {texto
        .toUpperCase()
        .replaceAll(" ", "-")
        .split("")
        .map((letra, i) => {
          return letra != "-" ? (
            <span key={i} style={{ color: colores[i], margin: 0 }}>
              {letra}
            </span>
          ) : (
            <span key={i} style={{ left: "2em", opacity: 0 }}>
              __
            </span>
          );
        })}
    </>
  );
}

export default App;
